import React, {useEffect} from 'react';
import { connect } from 'react-redux';
import styles from './CarrouselAccesories.module.css'
import { CardsCarrousel } from './subcomponents/CardsCarrousel/CardsCarrousel';

export function CarrouselAccesories({cardsToShow}) {
    const [cardElement,setCardElement] = React.useState([])
    React.useEffect(()=>{
        const length = cardsToShow.length
        const cardElementToShow = []
        for (var i=0;i<length;i++)
        {
            cardElementToShow.push(<CardsCarrousel key={'card-carrousel-'+i} {...cardsToShow[i]}></CardsCarrousel>)
        }
        setCardElement(cardElementToShow)
        
    },[cardsToShow])

    return (
        <div className={`${styles.wrapperCarrousel}`}>
           {cardElement}
        </div>
    )
}