import React, {useEffect, useId} from 'react';
import { connect } from 'react-redux';
import styles from './VideoHome.module.css'

function getTypeOfViewport (width)
{
    var viewportVideo = 'none'
    
    if (width>768) viewportVideo = 'https://seatmo-video.s3.eu-west-1.amazonaws.com/SEAT_MO_WEB_2560x1337_V12_test-loop_RESIZE.mp4'
    else if (width<= 768) viewportVideo = 'https://seatmo-video.s3.eu-west-1.amazonaws.com/SEAT_MO_WEB_1080x1920_V12_test-loop_RESIZE.mp4'
    return viewportVideo
}

export function VideoHome(props) {
    const videoId = useId()
    const viewport = getTypeOfViewport (window.innerWidth)

    useEffect(()=>{
        const videoElement = document.getElementById(videoId)
        videoElement.autoplay = true
        videoElement.muted = true
        videoElement.play()
    },[])
    return (
        <div style={{backgroundColor: '#EFEBE6', paddingBottom: '184px'}}>
           
            <video controls style={{width:'100vw', height: '100vh', objectFit: 'cover'}} id={videoId}>
                <source width="320" height="240" src={viewport} type="video/mp4"></source>
            </video>
           
        </div>
    )
}