import React, {useEffect} from 'react';
import styles from './DriveModeComponent.module.css'
import MainWindowStyles from '../../App.module.css'


export function DriveModeComponent({cardTitle, cardSubtitle, cardDescription, imgSrc }) {
    const [cardDescriptionElement,setCardDescriptionElement] = React.useState([])
    React.useEffect(()=>{
        const length = cardDescription.length
        const cardElementToShow = []
        for (var i=0;i<length;i++)
        {
            cardElementToShow.push(<div key={'app-discover-bt-'+i} className={styles.boldText}>{cardDescription[i].title}</div>)
            cardElementToShow.push(<div key={'app-discover-st-'+i} className={styles.simpleText}>{cardDescription[i].text}</div>)
            cardElementToShow.push(<div key={'app-discover-hl-'+i} className={styles.horizontalLine}> </div>)
        }
        setCardDescriptionElement(cardElementToShow)
        
    },[cardDescription])


    return (

        <div style={{backgroundColor: '#EFEBE6', paddingBottom: '184px'}}>
            
        <div className={`${MainWindowStyles.wrapperMainWindow} ${styles.componentWrapper}`}>
        
           <div className={`${styles.subWrapper} ${styles.cardText}`}>
                   
                <div className={styles.cardTitle}>{cardTitle}</div>
                <div className={styles.cardSubtitle}>{cardSubtitle} </div>
                <div className={styles.horizontaFirstlLine}></div>
                {cardDescriptionElement}
                
           </div>
           <div className={styles.imageContainer}>
                        <img style={{maxHeight: '432px'}} src={imgSrc} alt="Scooter" width={'100%'} height={'100%'}/>
                    </div>    
        </div>
        </div>
    )
}
