import React from 'react';
import styles from './StepsModule.module.css'
import { OneDescriptor } from './subcomponents/OneDescriptor/OneDescriptor';
export function StepsModule ({cardDescription, imgSrc }) {
   
    const [cardDescriptionElement,setCardDescriptionElement] = React.useState([])
    React.useEffect(()=>{
        const length = cardDescription.length
        const cardElementToShow = []
        for (var i=0;i<length;i++)
        {
            cardElementToShow.push(<OneDescriptor key={'steps-module-num-'+i} {...cardDescription[i]}/>)
        }
        setCardDescriptionElement(cardElementToShow)
        
    },[cardDescription])
    return (
        <div className={styles.wrapperStepsModule}>
            <div className={styles.subWrapperStepsModule}>
                <div className={styles.imageContainer}>
                            <img  src={imgSrc} alt="SeatMo" width={'100%'} />
                </div>
                <div className={styles.wrapperDescriptors}>
                    {cardDescriptionElement}
                </div>
            </div>
        </div>
    )
}