import React from 'react';
import styles from './IconCardsModule.module.css'
import { IconCard } from './subcomponents/IconCard/IconCard';
export function IconCardsModule ({title,cardDescription}) {
    const [cardDescriptionElement,setCardDescriptionElement] = React.useState([])
    React.useEffect(()=>{
        if (cardDescription)
        {
            const length = cardDescription.length
            const cardElementToShow = []
            for (var i=0;i<length;i++)
            {
                cardElementToShow.push(<IconCard key={'ic-card-module-'+i} {...cardDescription[i]}/>)
            }
            setCardDescriptionElement(cardElementToShow)}
        },[cardDescription])
    return (
        <div className={styles.wrapper}>
            <div className={styles.title}>{title}</div>
            <div className={styles.iconCardsWrapper}>
                {cardDescriptionElement}
            </div>
        </div>
    )
}