import React from 'react';
import { connect } from 'react-redux';
import styles from './RightDescriptionComponent.module.css'
import MainWindowStyles from '../../App.module.css'
import { SecondaryButton } from '../buttons/SecondaryButton/SecondaryButton';
import { VideoButton } from '../buttons/VideoButton/VideoButton'
const  separateIntoSpans = (text, wordToHighlight) => {
    const separateText = text.split(wordToHighlight)
    const spansToAppend = []
    for (var i=0; i<separateText.length; i++)
    {
     

        var spanElementRegular = <span key= {`RegularSpan ${i}` } className={styles.regularSpanPrimaryDescription}>{separateText[i]}</span>
        spansToAppend.push(spanElementRegular)

        var spanElementHighlighted = <span key= {`HighlightSpan ${i}` } className={styles.HighlightSpanPrimaryDescription}> {wordToHighlight}</span>
        spansToAppend.push(spanElementHighlighted)
    }
    if (separateText.length>1) spansToAppend.pop()
    
    return spansToAppend


}

export function RightDescriptionComponent(props) {
    const spans = separateIntoSpans(props.primaryText,props.primaryHighlight )
   
    var SecondaryButtonElement = null
    var secondaryDescriptionButtonElement = null

if (props.primaryButtonText) SecondaryButtonElement = 
    
    <div className={styles.primaryDescriptionButton}>
    <SecondaryButton textButton={props.primaryButtonText}/>
    </div>

if (props.secondaryButtonText) secondaryDescriptionButtonElement = 

    <div className={styles.secondaryDescriptionButton}>
    <VideoButton textButton={props.secondaryButtonText}></VideoButton>
    </div>   

    return (
        <div className={`${MainWindowStyles.wrapperMainWindow} ${styles.componentWrapper}`}>
            
     
                <div className={styles.primaryDescription}>
                    <div className={styles.textPrimaryDescription}>
                       {spans}
                    </div>
                </div>
                {SecondaryButtonElement}
                <div className={styles.secondaryDescription}>
                    <div className={styles.textSecondaryDescription}>
                        <span>{props.secondaryText}</span>
                </div>
                {secondaryDescriptionButtonElement}
                </div>

            
        </div>
    )
}